import {
  createStyles,
  ModalContainer,
  ModalContent,
  ModalHeader,
  ModalHeaderAdornment,
  ModalContainerAdornment,
  ModalXButton,
} from '@instacart/cocktail'
import { responsive } from '@instacart/ids-core'
import { useLang } from '@app/services/config/hooks'
import { useIsMobile } from '@app/react/common/hooks/useIsMobile'
import AssetImage from '../../common/AssetImage'

export type PolicyModalProps = {
  onClose: () => void
}

const useStyles = createStyles(() => ({
  container: {
    height: '100%',
    [responsive.only('c')]: {
      margin: 0,
      width: 'auto',
    },
  },
  emphasizedText: {
    fontWeight: 'bold',
    margin: 0,
  },
  logo: {
    height: '80px',
  },
  logoWrapper: {
    paddingBottom: '40px',
    paddingTop: '40px',
    textAlign: 'center',
  },
  mobileHeader: {
    paddingBottom: `32px`,
    paddingTop: `32px`,
  },
  mobileLogoWrapper: {
    paddingBottom: '18px',
    paddingTop: '0',
    textAlign: 'center',
  },
  mobileTabContent: {
    borderTop: '8px solid #F2F2F2',
    padding: `32px 20px`,
  },
  modalContent: {
    backgroundColor: 'unset',
    padding: 0,
  },
  modalHeaderAdornment: {
    padding: '0 10px',
  },
  tab: {
    fontWeight: 'bold',
    padding: `24px`,
  },
  tabContent: {
    borderTop: '8px solid #F2F2F2',
    padding: `24px 24px 40px`,
  },
  tabContentBody: {
    color: '#343538',
    fontFamily: 'Eina',
    fontSize: '16px',
    lineHeight: '25px',
  },
  tabContentHeader: {
    fontSize: '18px',
    fontWeight: 'bold',
    marginBottom: '16px',
    marginTop: 0,
  },
  tabs: {
    borderBottom: '8px solid #F2F2F2',
    borderTop: '1px solid #D8D8D8',
    display: 'flex',
    justifyContent: 'center',
  },
  text: {
    margin: 0,
  },
}))

const PolicyModal = (props: PolicyModalProps) => {
  const { onClose } = props
  const isMobile = useIsMobile()
  const styles = useStyles(props)
  const lang = useLang()

  return (
    <ModalContainer css={styles.container} roundedCorners={!isMobile}>
      {isMobile && (
        <ModalHeader className="modal-header" css={styles.mobileHeader}>
          <ModalHeaderAdornment css={styles.modalHeaderAdornment} position="right">
            <ModalXButton onClick={onClose} />
          </ModalHeaderAdornment>
        </ModalHeader>
      )}
      {!isMobile && (
        <ModalContainerAdornment className="modal-header-adornment">
          <ModalXButton onClick={onClose} />
        </ModalContainerAdornment>
      )}
      <ModalContent className="modal-content" css={styles.modalContent} layout="large">
        <div css={isMobile ? styles.mobileLogoWrapper : styles.logoWrapper}>
          <AssetImage css={styles.logo} src={'images/header-logo.svg'} alt={lang.clientName} />
        </div>
        {/* TODO: We want to support multiple tabs for additional policies in the future e.g. alcohol */}
        {/* <ul role="tablist" css={styles.tabs}>
          <li
            id="policy-tab-pricing"
            tabIndex={0}
            aria-controls="pricing-policy"
            role="tab"
            css={styles.tab}
            aria-selected="true"
          >
            {lang.pricingPolicyLabel}
          </li>
        </ul> */}
        <div
          id="pricing-policy"
          role="tabpanel"
          tabIndex={0}
          aria-labelledby="policy-tab-pricing"
          css={isMobile ? styles.mobileTabContent : styles.tabContent}
        >
          <h3 css={styles.tabContentHeader}>{lang.pricingPolicyLabel}</h3>
          <div css={styles.tabContentBody}>
            <p css={styles.emphasizedText}>{lang.pricingPolicyEmphasizedText}</p>
            <p css={styles.text}>{lang.pricingPolicyText}</p>
          </div>
        </div>
      </ModalContent>
    </ModalContainer>
  )
}

export default PolicyModal
